import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
export default new Router({
  // mode:'history',
  // base:'/baohu/',
  routes: [
    {
      path: '/',
      redirect: '/login',
    },
    {
      path: '/login',
      name: 'login',
      component: ()=>import("@/components/login")
    },
    {
      path: '/list',
      name: 'list',
      component: ()=>import("@/components/list"),
      meta:true
    },
    // {
    //   path: '/level',
    //   name: 'level',
    //   component: ()=>import("@/components/level"),
    //   meta:true
    // },
    // {
    //   path: '/withdrawal',
    //   name: 'withdrawal',
    //   component:()=>import("@/components/withdrawal")
    // },
    // {
    //   path: '/protect',
    //   name: 'protect',
    //   component: ()=>import("@/components/protect")
    // },
    // {
    //   path: '/Explain',
    //   name: 'Explain',
    //   component: ()=>import("@/components/Explain")
    // },

    // {
    //   path: '/Promoter',
    //   name: 'Promoter',
    //   component:()=>import("@/components/Promoter")
    // },
    {
      path: '/tip',
      name: 'tip',
      component: ()=>import("@/components/tip")
    },
    {
      path: '/Detailed',
      name: 'Detailed',
      component:()=>import("@/components/Detailed")
    }
  ]
})




