import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        Load: false,
        tip:''
      },
      mutations: {
        Loadchange (state,params) {
          // 变更状态
          state.Load = params.Load
          state.tip = params.tip
        }
      }
})

export default store


