// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'lib-flexible/flexible'
import fastClick from 'fastClick'
import store from './store'
import { Field,CellGroup,Toast,Button,Dialog,List,Cell,Icon,Radio,RadioGroup,Loading,PullRefresh,Tab, Tabs,Rate  } from 'vant';
import 'vant/lib/icon/local.css';
Vue.use(Field).use(CellGroup)
.use(Toast).use(Button).
use(Dialog).use(List).
use(Cell).use(Icon).
use(Radio).use(RadioGroup).
use(Loading).use(PullRefresh).
use(Tab).use(Tabs).use(Rate);

Vue.config.productionTip = false
fastClick.attach(document.body)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



